'use strict';

class Project {

    /** @type string */ title;

    /** @type string */ description;

    /** @type string */ image;

    /** @type string */ url;

    /** @type [string] */ tags;

    /** @type [boolean] */ span;

    constructor(title, description, image, url, tags, span) {
        this.title = title;
        this.description = description;
        this.image = image;
        this.url = url;
        this.tags = tags;
        this.span = span;
    }
}

export default Project;



